import React from "react"
import { graphql, Link } from "gatsby"
import _ from 'lodash';

import Card from 'react-bootstrap/Card';
import CardColumns from 'react-bootstrap/CardColumns';
import Row from 'react-bootstrap/Row';
import Jumbotron from 'react-bootstrap/Jumbotron';

import Layout from "../components/layout"
import DatasetCard from "../components/dataset-card";

export default ({ data }) => {
  return (
    <Layout>
      <Row>
        <Jumbotron className="w-100">
          <div  style={{ maxWidth: '700px' }}>
            <h1 class="display-4">Catafolk</h1>
            <p className="lead">
              A catalogue of folk music datasets for computational ethnomusicology
            </p>
            <hr class="my-4" />
            <p>
              The project hopes to make datasets of folk music more easily accessible. 
              It provides a reference of datasets, but also aims to include some basic automatic analyses.
            </p>
          </div> 
        </Jumbotron>
      </Row>
      <Row className="mt-5">
        <CardColumns>
          {data.allDataset.edges.map(node => (
            <DatasetCard dataset={node.dataset} />
          ))}
          <Card bg="dark" text="light">
            <Card.Body>
              <Card.Title>And more...</Card.Title>
              <Card.Text>
                <p>Catafolk currently contains {data.allDataset.totalCount} datasets.</p>
                <Link to="/datasets/" class="btn btn-light">View all datasets</Link>
              </Card.Text>
            </Card.Body>
          </Card>
        </CardColumns>
      </Row> 
    </Layout>
  )
}

export const query = graphql`{
  allDataset {
    totalCount
    edges {
      dataset: node {
        dataset_id
        description
        fields {
          slug
        }
        title
        tags
      }
    }
  }
}`
